<template>
    <div>
        <div v-if="option.items && option.items.length >= 1">
            <div v-for="item in option.items" :key="item.AffiliateCommitteeId">
                <b-form-group>
                    <b-form-checkbox
                        v-model="selected"
                        :value="item.AffiliateCommitteeId"
                    >
                        {{ item.AffiliateCommitteeName }} - {{ item.Description }}
                    </b-form-checkbox>
                </b-form-group>
            </div>
        </div>
        <div v-else>
            <p>No data</p>
        </div>
    </div>
</template>

<script>
export default {
    name: 'DownloadOptionCheckList',
    props: {
        option: { type: Object, required: true },
    },
    data: () => ({
        selected: [],
    }),
    watch: {
        selected() {
            console.log('selected', this.selected);
            this.$emit('input', this.selected);
        },
    },
};
</script>
