    <div id="searchIndividual" class="mt-4">
        <form autocomplete="off">
            <div class="row">
                <div class="col-md">
                    <v-text-field
                        v-model="search.nameFirst"
                        label="First Name"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="First Name">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-user"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
                <div class="col-md">
                    <v-text-field
                        v-model="search.nameMiddle"
                        label="Middle Name"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="Middle Name">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-user"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
                <div class="col-md">
                    <v-text-field
                        v-model="search.nameLast"
                        label="Last Name"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="Last Name">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-user"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
            </div>
            <div class="row">
                <div class="col-md">
                    <v-text-field
                        v-model="search.memberid"
                        label="Member ID"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="Member ID">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-id-card"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
                <div class="col-md">
                    <v-text-field
                        v-model="search.affiliate"
                        label="Affiliate Name or Number"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="Affiliate Name or Number">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-list-alt"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
                <div class="col-md">
                    <v-select
                        v-model="search.parent"
                        label="State Federation"
                        :items="stateFeds"
                        item-value="AffiliateId"
                        item-title="AffiliateName"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="State Federation">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-sitemap"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-select>
                </div>
            </div>
            <div class="row">
                <div class="col-md">
                    <v-text-field
                        v-model="search.phone"
                        label="Phone Number"
                    >
                       <template #prepend-inner>
                            <v-tooltip text="Phone Number">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-phone"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
                <div class="col-md">
                    <v-text-field
                        v-model="search.city"
                        label="Home City"
                    >
                       <template #prepend-inner>
                           <v-tooltip text="Home City">
                               <template #activator="{ props }">
                                   <v-icon v-bind="props" icon="mdi:mdi-city"></v-icon>
