            <div class="row">
                <div class="col-md">
                    <v-text-field
                        v-model="search.namePreferred"
                        label="Preferred Name"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="Preferred Name">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-user"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
                <div class="col-md">
                    <v-text-field
                        v-model="search.email"
                        label="Email Address"
                    >
                        <template #prepend-inner>
                            <v-tooltip text="Email Address">
                                <template #activator="{ props }">
                                    <v-icon v-bind="props" icon="fa-at"></v-icon>
                                </template>
                            </v-tooltip>
                        </template>
                    </v-text-field>
                </div>
            </div>
            <div class="d-flex justify-content-center ga-2">
                <v-btn
                    name="clear"
                    :loading="loading"
                    @click="clearAll"
                    color="secondary"
                >
                    Clear All Fields
                </v-btn>
                <v-btn
                    name="search"
                    :loading="loading"
                    @click="searchIndividual"
                    color="primary"
                >
                    Search
                </v-btn>
            </div>
            <v-divider></v-divider>
        </form>
        <v-data-table-server
            v-model:items-per-page="tableOptions.per_page"
            v-model:page="tableOptions.current_page"
            v-model:items-length="tableOptions.total"
            :headers="tableHeaders"
            :items="tableResults"
            :loading="loading"
            @update:page="pageChanged"
            @update:items-per-page="perPageChanged"
        >
            <template v-slot:[`item.name`]="{ item }">
                {{ item.FullName }}
            </template>
            <template v-slot:[`item.relationships`]="{ item }">
                <DisplayRelationship
                    v-for="relationship in item.IndividualAffiliatesAll"
                    :key="relationship.Affiliate.AffiliateId"
                    :relationship="relationship"
                />
            </template>
            <template v-slot:[`item.officership`]="{ item }">
                <DisplayOfficer
                    v-for="officer in item.AffiliateOfficership"
                    :key="officer.AffiliateOfficerId"
                    :officer="officer"
                />
            </template>
            <template v-slot:[`item.phones`]="{ item }">
                <DisplayPhone v-for="phone in item.IndividualPhones" :key="phone.PhoneNumber" :phone="phone" />
            </template>
            <template v-slot:[`item.emails`]="{ item }">
                <DisplayEmail v-for="email in item.IndividualEmails" :key="email.Email" :email="email" />
            </template>
            <template v-slot:[`item.addresses`]="{ item }">
                <DisplayAddress v-for="address in item.IndividualAddresses" :key="address.AddressLine1" :address="address" />
            </template>
        </v-data-table-server>
    </div>
</template>

<script>
import axios from 'axios';
import { pickBy } from 'lodash-es';
import DisplayAddress from "./custom/DisplayAddress.vue";
import DisplayEmail from "./custom/DisplayEmail.vue";
import DisplayOfficer from "./custom/DisplayOfficer.vue";
import DisplayPhone from "./custom/DisplayPhone.vue";
import DisplayRelationship from "./custom/DisplayRelationship.vue";

export default {
    name: 'SearchIndividual',
    components: {
        DisplayAddress,
        DisplayEmail,
        DisplayOfficer,
        DisplayPhone,
        DisplayRelationship,
    },
    data: () => ({
        loading: false,
        search: {
            memberid: null,
            affiliate: null,
            parent: null,
            phone: null,
            city: null,
            state: null,
            nameFirst: null,
            nameMiddle: null,
            nameLast: null,
            namePreferred: null,
            email: null,
        },
        tableOptions: {
            current_page: 1,
            per_page: 15,
            total: 0,
        },
        tableHeaders: [],
        tableResults: [],
        stateFeds: [],
    }),
    mounted() {
        let urlSearch = false;
        const query = this.$route.query;
        Object.entries(query).forEach(([key, value]) => {
            if (key in this.search) {
                this.search[key] = value;
                urlSearch = true;
            }
        });
        this.getOptions()
            .then(() => {
                if (urlSearch) {
                    this.searchIndividual();
                }
            });
    },
    methods: {
        setUrl(search) {
            this.$router.push({ name: this.$route.name, query: search })
                .catch((err) => {
                    if (err.name !== 'NavigationDuplicated') { throw (err); }
                });
        },
        async getOptions() {
            this.loading = true;
            return axios.get('/api/staff/searchIndividual/options')
                .then((response) => {
                    this.tableOptions = response.data.options;
                    this.tableHeaders = response.data.headers;
                    this.stateFeds = response.data.statefeds;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        async searchIndividual() {
            this.loading = true;
            // pickBy removes null values from the search object for a prettier URL query.
            this.setUrl(pickBy(this.search));
            return axios.post('/api/staff/searchIndividual', { search: this.search, options: this.tableOptions })
                .then((response) => {
                    const { data, ...options } = response.data;
                    this.tableOptions = options;
                    this.tableResults = data;
                })
                .finally(() => {
                    this.loading = false;
                });
        },
        clearAll() {
            for (const [key] of Object.entries(this.search)) {
                this.search[key] = null;
            }
            this.setUrl(null);
        },
        pageChanged(page) {
            this.tableOptions.current_page = page;
            this.searchIndividual(page);
        },
        perPageChanged(perPage) {
            this.tableOptions.per_page = perPage;
            this.searchAffiliate();
        },
    },
};
</script>
